<template>
    <div class="all">
        <div id="header" style="width:100%;height:65px;background:#242f42;position:fixed;z-index:1;">
        <el-row>
            <el-col :span="24">
                <div class="grid-content bg-purple" style="width:35%;">
                    <div class="logo">
                        <img :src="logo" alt="玉带职业技术学校">
                    </div>
                    <div class="title">
                        <p style="letter-spacing:10px;">玉带职业技术学校<p/>
                        <div class="l_title">教学诊断与改进系统后台管理</div>
                    </div>
                </div>
                <div class="grid-content bg-purple-light" style="width:65%;float:right;">
                    <div style="float:right;">
                        <el-input
                            placeholder="请输入内容"
                            prefix-icon="el-icon-search"
                            v-model="search"
                            clearable
                            style="width:230px;margin-top:12px;">
                        </el-input>
                        <el-button 
                        type="primary" 
                        icon="el-icon-search" 
                        style="margin-top:12px;background-color: #029348;border:1px solid #029348;">
                            搜索
                        </el-button>
                    <ul class="headerNav">
                        <li class="userInfo">
                            <div class="info">
                                <span class="name">{{name}}</span>
                                <span class="zw">教职工</span>
                            </div>
                            <div class="tx"><img :src="touxiang" alt=""></div>
                        </li>
                        <li class="loginout">
                            <el-dropdown trigger="click" @command="handleCommand">
                                <span class="el-dropdown-link">
                                    <div class="tuichu">
                                        <i class="iconfont icon-tuichu" style="font-size:20px;"></i>
                                    </div>
                                </span>
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item><span @click="tuichu">退出登录</span></el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                        </li>
                    </ul>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
        <div class="left">
            <el-menu router
            class="el-menu-vertical-demo"
            default-active="/index"
            @open="handleOpen"
            @close="handleClose"
            background-color="#1C2228"
            text-color="#fff"
            active-text-color="#ffd04b">
                <el-menu-item class="item" index="/index">
                    <i class="el-icon-location"></i>
                    <span slot="title">学校机构设置</span>
                </el-menu-item>
                <el-menu-item index="/renyuan">
                    <i class="el-icon-menu"></i>
                    <span slot="title">学校人员管理</span>
                </el-menu-item>
                <el-menu-item index="/canshu">
                    <i class="el-icon-document"></i>
                    <span slot="title">运行参数设置</span>
                </el-menu-item>
                <el-menu-item index="/quanxian">
                    <i class="el-icon-setting"></i>
                    <span slot="title">指标权限管理</span>
                </el-menu-item>
            </el-menu>
        </div>
        <div class="right">
            <router-view/>
        </div>
    </div>
</template>

<script>
import store from '../store/store.js';
export default {
    name:"index",
    data(){
        return{
            logo: 'http://cdn.xqit.net/zg/img/yd2.png',
            touxiang: 'http://cdn.xqit.net/zg/img/laoshi.png',
            search:'',
            name: '倪老师',
        }
    },
    methods:{
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        tuichu(){
            console.log("411")
            this.$router.push({ name: 'HelloWorld'})　
        }
    }
}
</script>

<style scoped>
.all{
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.left{
    float: left;
    position: absolute;
    height: 550px;
    margin-top: 65px;
}
.right{
    position: absolute;
    margin-left: 200px;
    margin-top: 65px;
    /* width: 89.6%;
    height: 93.3%; */
    width: 1150px;
    height: 550px;
}
.item{
    margin-top: 30px;
}
.el-menu-vertical-demo{
    width: 200px;
    height: 100%;
}
.grid-content{
    height: 65px;
    float: left;
    overflow: hidden;
}
.logo{
    width: 65px;
    height: 65px;
    float: left;
    /* margin: 0 20px 0; */
}
.logo img{
    width: 65px;
    height: 65px;
}
.title{
    /* width: 370px; */
    height: 27px;
    float: left;
    font-size: 20px;
    line-height: 27px;
    padding-left: 30px;
    text-align: left;
    color: #fff;
    display: inline-block;
    margin-top: 10px;
}
.l_title{
    float: left;
    font-size: 14px;
    text-align: left;
    color: #fff;
    letter-spacing: 7.5px;
    font-family: 'KaiTi';
}
.headerNav{
    float: right;
    display: flex;
    color: #fff;
}
.userInfo{
    width: 80px;
    height: 65px;
    padding: 0 20px;
    cursor: pointer;
    display: flex;
    align-items: center;
}
.info{
    width: 42px;
    height: 36px;
    float: left;
    line-height: 18px;
    margin-right: 5px;
}
.name{
    display: block;
    text-align: right;
    font-size: 14px;
}
.zw{
    color: rgb(186, 186, 186);
    font-size: 12px;
    float: right;
    text-align: right;
}
.tx{
    width: 30px;
    height: 30px;
    float: right;
}
.tx img{
    width: 100%;
    height: 100%;
}
.loginout{
    line-height: 65px;
    padding: 0 20px;
    cursor: pointer;
}
.tuichu{
    color: #ffffff;
}
</style>



